/* page format */
.App {
  text-align: center;
  color: white;
}

.container {
  display: flex;
  flex-direction: row;
}

.left_column {
  width: 20%;
  height: 97vh;
  flex-direction: flex-start;
  border-right: 3px solid white;
}

.main_content {
  width: 85%;
  height: 10000%;
  flex-direction: flex-end;
}

.main_content::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #10042C;
  z-index: -1;
}

.table_container {
  display: flex;
  flex-direction: wrap;
  margin-left: 1%;

}

.edit_container {
  margin-left: 1%;
}

.save_button {
	background-color:#ffffff;
	border-radius:6px;
	color:#666666;
	font-family:Arial;
	font-size:22px;
	font-weight:bold;
  padding:27px 23px;
}

/* table content */ 
table {
  width: 90%;
  border-collapse: collapse;
  margin-bottom: 20px;
  position: absolute;
  width: 79%;
  height: 98%;
  background: #2F1087;
  border-radius: 40px;
}

thead {
  background-color: #21104D;
}

th {
  padding: 8px;
  text-align: left;
  color: #ffffff;
}

td {
  padding: 8px;
  text-align: left;
  color: #ffffff;
}

th:first-child {
  text-align: center;
  border-top-left-radius: 40px;
  border-top-right-radius: 0px;
}

th:last-child {
  text-align: center;
  border-top-left-radius: 0px;
  border-top-right-radius: 40px;
}

td:first-child {
  text-align: center;

  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
}

td:last-child {
  text-align: center;

  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
}

tr:hover {
  background-color: #21104D;
}

pre {
  background-color: #f8f8f8;
  padding: 10px;
  font-family: 'Courier New', Courier, monospace;
  white-space: pre-wrap;
}
.TextBoxDoc {
  padding: 0;
  height: 30px;
  position: relative;
  left: 0;
  outline: #A85AA4;
  border: 1px solid #cdcdcd;
  border-color: #A85AA4;
  background-color: #2F1087;
  font-size: 16px;
}


